$font-stack:    'Barlow Semi Condensed', sans-serif;
$primary-color: #333;

$dark-text: hsl(229, 25%, 31%);
$score-text: hsl(229, 64%, 46%);
$border-outline: 3px solid hsl(217, 16%, 45%);

$main-radial-gradient: radial-gradient(circle at top center, hsl(214, 47%, 23%), hsl(237, 49%, 15%));
$scissors-radial-gradient: linear-gradient(to right,  hsl(39, 89%, 49%) , hsl(40, 84%, 53%));
$paper-radial-gradient: linear-gradient(to right,  hsl(230, 89%, 62%) , hsl(230, 89%, 65%));
$rock-radial-gradient: linear-gradient(to right,  hsl(349, 71%, 52%) , hsl(349, 70%, 56%));
$lizard-radial-gradient: linear-gradient(to right,  hsl(261, 73%, 60%) , hsl(261, 72%, 63%));
$spock-radial-gradient: linear-gradient(to right,  hsl(189, 59%, 53%) , hsl(189, 58%, 57%));

$icon-width: 30px;
$pentagon-width: 200px;
$grid-dim: ($pentagon-width / 2);
$grid-half-dim: ($grid-dim / 2);
$grid-quarter-dim: ($grid-dim / 4);
$grid-spacer: ($grid-half-dim / 10);




.attribution {
  font-size: 11px; text-align: center; 

  a { 
    color: hsl(228, 45%, 44%); 
  }
}

.wrapper {
  display:block;
  
  .main-background {
    background-image: $main-radial-gradient;
    height: 1200px;
    display:flex;
    flex-direction: column;
    
    .main-title {
      border: $border-outline;
      border-radius: 10px;
      display:flex;
      justify-content: space-between;
      margin-top:60px;
      margin-left:auto;
      margin-right:auto;
      padding:1rem;
      width:80%;
      
      .options-list {
        list-style: none;
        font-family: $font-stack;
        font-weight:700;
        
        li {
          font-size: 20px;
          color: white;
          text-transform: uppercase;
        }
      }
      
      .scoreboard {
        align-items: center;      
        background-color: white;
        border-radius: 10px;
        font-family: $font-stack;
        text-transform: uppercase;
        width: 4rem;
        display:flex;
        flex-direction: column;
        padding: 1rem 1.5rem;
        
        p {
          letter-spacing: 1.5px;
          font-size:15px;
          font-weight:600;
          color: hsl(229, 64%, 46%);
        }
        
        #score-output{
          font-size: 60px;
          font-weight:700;
          color: $dark-text;
        }
      }
    }

    .selection-layout {
      padding: 4rem 0;
      height: 400px;
      display: grid;
      grid-template-columns:  [line1] auto [line2] $grid-half-dim [line3] $grid-half-dim 
                              [line4] $grid-quarter-dim [line5] $grid-quarter-dim [line6] $grid-quarter-dim [line7] $grid-quarter-dim [line8] $grid-quarter-dim [line9] $grid-quarter-dim
                              [line10] $grid-half-dim [line11] $grid-half-dim [line12] auto [end];
      grid-template-rows: [row1] $grid-half-dim [row2] $grid-quarter-dim [row3] $grid-quarter-dim [row4] $grid-half-dim [row5] $grid-quarter-dim [row6] $grid-quarter-dim [row7] $grid-half-dim [row8] $grid-half-dim [row9] $grid-dim [row10] auto [end];

      .pentagon {
        margin-left: auto;
        margin-right: auto;
        grid-column: line2 / line12;
        grid-row-start: row2;
        width: $pentagon-width;
      } 

      .circlebase {
        border-radius: 50%;
        padding:10px;
        border: 12px solid transparent;       
        width: 100%;
        height: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
      }
      
      .icon {
        width: $icon-width;
      }

      .rock-div {
        grid-column: line8 / span 3;
        grid-row: row7 / span 2;
        background: 
        linear-gradient(#fff,#fff) padding-box, /*this is your white background*/
        $rock-radial-gradient border-box;
      }
      
      .paper-div {
        grid-column: line10 / span 2;
        grid-row: row3 / span 3;
        background: 
        linear-gradient(#fff,#fff) padding-box, /*this is your white background*/
        $paper-radial-gradient border-box;
      }

      .scissors-div {
        grid-column: line5 / span 4;
        grid-row: row1 / span 3;
        background: 
        linear-gradient(#fff,#fff) padding-box, /*this is your white background*/
        $scissors-radial-gradient border-box;
      }

      .lizard-div {
        grid-column: line3 / span 3;
        grid-row: row7 / span 2;
        background: 
        linear-gradient(#fff,#fff) padding-box, /*this is your white background*/
        $lizard-radial-gradient border-box;
      }

      .spock-div {
        grid-column: line2 / span 2;
        grid-row: row3 / span 3;
        background: 
        linear-gradient(#fff,#fff) padding-box, /*this is your white background*/
        $spock-radial-gradient border-box;
      }



    }
    
    .rules-button {
      border: $border-outline;
      border-radius: 10px;
      margin-left:auto;
      margin-right:auto;
      font-family: $font-stack;
      font-weight:600;
      font-size: 20px;
      width:50%;
      color: white;
      text-transform: uppercase;
      text-align: center;
      letter-spacing: 3px;
      padding: 1rem 0; 
    }
  } 
}
  
  